import React,{
  useEffect,
  useState,
  useRef,
  useCallback,
  useMemo
} from 'react';
import {StudentLoader} from '../components/studentLoader';
import {TeacherLoader} from '../components/teacherLoader';
import {Rating} from './rating';
import {Timmer} from '../components/timmer';
import {io} from 'socket.io-client';
const rootUrl=true?"https://solvedudar.in":"http://localhost:7070";
const socket=io.connect(rootUrl);
export const Room=({drone})=>{
    const ref=useRef(0);
    const room=useRef();
    const pc=useRef();
    const userData=useRef();
    const teacherName=useRef();
    const teacherProfilePic=useRef();
    const [loading,setLoading]=useState(true);
    const [isTeacherJoinTheRoom,setIsTeacherJoinTheRoom]=useState(false);
    const [isVideoOn,setIsVideoOn]=useState(false);
    const [isAudioOn,setIsAudioOn]=useState(true);
    const [cutCall,setCutCall]=useState(false);
    const [showRatingComponent,setShowRatingComponent]=useState(false);
    const [isTeacherVedioOn,setIsTeacherVedioOn]=useState(false);
    const [teacherLocalVedioWidth,setTeacherLocalVedioWidth]=useState(false);
    const ratingTags=useRef();
    const duration=useRef(0);
    const timmerId=useRef();
    const countDuration=(mode)=>{
      if(mode==="start"){
       timmerId.current = setInterval(() => {
          duration.current=duration.current+1;
         }, 1000);
      }else{
        clearInterval(timmerId.current);
      }
    }
    const roomHash = useMemo(()=>{
     userData.current=window.location.hash.substring(1).split('?')[1].split("=");
     return window.location.hash.substring(1).split('?')[0];
    },[]);
    const roomName = useMemo(()=>{
      return 'observable-' + roomHash;
    },[]);
    const configuration = useMemo(()=>({
      iceServers: [
        {
          urls: 'stun:stun.l.google.com:19302'
        },
        { 
          urls: "turn:turn.edumaxe.in:5349",
          username: "dudar",
          credential: "dudar",
        },
        {
          urls: "turn:openrelay.metered.ca:443",
          username: "openrelayproject",
          credential: "openrelayproject",
        },
        {
          urls: "turn:openrelay.metered.ca:443?transport=tcp",
          username: "openrelayproject",
          credential: "openrelayproject",
        },
      ],
      }),[])
      useEffect(()=>{
        const fetchRatingTags = async data => {
            try {
              const response = await fetch(
                `${rootUrl}/ratingTags`,
                {
                  method: 'GET',
                  headers: new Headers({
                    'Content-Type': 'application/json',
                  }),
                },
              );
              let resdata = await response.json();
              if (response.status === 200 || response.ok) {
                if (resdata) {
                  ratingTags.current=resdata.review_data;
                }
              } else {
                // handle server error
                console.log('error',resdata);
              }
            } catch (e) {
              console.log('application internal error', e);
            }
          };
          fetchRatingTags();
    },[]);
    useEffect(()=>{
      ref.current=ref.current+1;
    });
    useEffect(()=>{
      drone.on('open', error => {
        drone.publish({
          room: window.location.hash.substring(1).split('?')[0],
          message:{...userData.current}
        });
      });
      if( userData.current[0]==="student"){
        drone.on('open', error => {
          const room = drone.subscribe(window.location.hash.substring(1).split('?')[0]);
          room.on('data', message => {
            teacherName.current=message[1];
            teacherProfilePic.current=message[2];
          });
        });
      }
    },[]);
    useEffect(()=>{
      socket.emit('join-room',window.location.hash.substring(1).split('?')[0]);
    },[]);
    if (!window.location.hash) {
        window.location.hash = Math.floor(Math.random() * 0xFFFFFF).toString(16);
      }
     function onSuccess() {};
     function onError(error) {
      console.error(error);
     };
     const cutAllCall=()=>{
      drone.close();
      setCutCall(true);
      setShowRatingComponent(true);
      socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
     }
     useEffect(()=>{
      socket.on('cut_call',payload=>{
        setCutCall(true);
        drone.close();
        setShowRatingComponent(true);
      });
    },[]);
    useEffect(()=>{
      socket.on('toggle-video',payload=>{
        setIsTeacherVedioOn(payload.isTeacherVedioOn);
      });
    },[]);
    useEffect(()=>{
      drone.on('open', error => {
        if (error) {
          return console.error(error);
        }
        room.current = drone.subscribe(roomName);
        room.current.on('open', error => {
          if (error) {
            onError(error);
          }
        });
        // We're connected to the room and received an array of 'members'
        // connected to the room (including us). Signaling server is ready.
        setLoading(!loading);
        room.current.on('members', members => {
          console.log('MEMBERS', members);
          // If we are the second user to connect to the room we will be creating the offer
          if(userData.current[0]==="teacher"){
            if(members.length===1){
              setTimeout(() => {
                updateDuration(rootUrl,window.location.hash.substring(1).split('?')[0]);
                drone.close();
                setCutCall(true);
                setShowRatingComponent(true);
                socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
              },2000);
            }
          }
          const isOfferer = members.length === 2;
          if(isOfferer) {
            console.log('connecting with student');
            //setIsTeacherJoinTheRoom(true);
          }
          startWebRTC(isOfferer);
        });
      });
    },[])

    // Send signaling data via Scaledrone
    const sendMessage = useCallback( (message)=>{
      drone.publish({
        room: roomName,
        message
      });
     },[]);

   const startWebRTC=useCallback(isOfferer=> {
    pc.current = new RTCPeerConnection(configuration);
    // 'onicecandidate' notifies us whenever an ICE agent needs to deliver a
    // message to the other peer through the signaling server
    pc.current.onicecandidate = event => {
      if (event.candidate) {
        sendMessage({'candidate': event.candidate});
      }
    };
    // If user is offerer let the 'negotiationneeded' event create the offer
    if (isOfferer) {
        pc.current.onnegotiationneeded = () => {
            pc.current.createOffer().then(localDescCreated).catch(onError);
      }
    }
    // When a remote stream arrives display it in the #remoteVideo element
    pc.current.ontrack = event => {
      const stream = event.streams[0];
      if (!window.remoteVideo.srcObject || window.remoteVideo.srcObject.id !== stream.id) {
        window.remoteVideo.srcObject = stream;
      }
    };
    navigator.mediaDevices.getUserMedia({
      audio: true,
      video: {
        facingMode:userData.current[0]==="student"? 'user' :'environment'
      },
    }).then(stream => {
      // Display your local video in #localVideo element
      window.localVideo.srcObject = stream;
      toggleVideo();
      // Add your stream to be sent to the conneting peer
      pc.current.addStream(stream);
    }, onError);
    // Listen to signaling data from Scaledrone
    room.current.on('data', (message, client) => {
      // Message was sent by us
      if (client.id === drone.clientId) {
        return;
      }
      if (message.sdp) {
        // This is called after receiving an offer or answer from another peer
        pc.current.setRemoteDescription(new RTCSessionDescription(message.sdp), () => {
          // When receiving an offer lets answer it
          if (pc.current.remoteDescription.type === 'offer') {
            console.log('teacher connected');
            setIsTeacherJoinTheRoom(true);
            countDuration("start");
            pc.current.createAnswer().then(localDescCreated).catch(onError);
          }
        }, onError);
      } else if (message.candidate) {
        // Add the new ICE candidate to our connections remote description
        pc.current.addIceCandidate(
          new RTCIceCandidate(message.candidate), onSuccess, onError
        );
      }
    });
  },[]);
  const localDescCreated=useCallback(desc=>{
    pc.current.setLocalDescription(
      desc,
      () => sendMessage({'sdp': pc.current.localDescription}),
      onError
    );
  },[]);
  const toggleVideo= ()=> {
    const stream = window.localVideo.srcObject;
    const videoTrack = stream.getTracks().find(track => track.kind === 'video');
    if (videoTrack.enabled) {
      videoTrack.enabled = false;
   } else {
      videoTrack.enabled = true;
   }
   setIsVideoOn(!isVideoOn);
   socket.emit('toggle-video',{
     room:window.location.hash.substring(1).split('?')[0],
     isTeacherVedioOn:isVideoOn
  });
  };
  const toggleAudio= ()=> {
    const stream = window.localVideo.srcObject;
    const audioTrack = stream.getTracks().find(track => track.kind === 'audio');
    if (audioTrack.enabled) {
      audioTrack.enabled = false;
   } else {
      audioTrack.enabled = true;
   }
   setIsAudioOn(!isAudioOn);
  }
  return(
  <div style={{
    backgroundColor:'#e6fff0',
    height:'100vh'
  }}>
     {
       userData.current[0]==="student" && !showRatingComponent&&(<div>
         {
           isTeacherJoinTheRoom&&(
           <div className="msg">
             <div style={{display:'flex',flexDirection: 'row',alignItems:'center',width:window.innerWidth}}>
           <div style={{display:'flex',flexDirection: 'row',alignItems:'center',width:window.innerWidth}}>
           <img 
             style={{width:'50px',height:'50px',borderRadius:'50px',margin:'5px'}} 
             src={teacherProfilePic.current} 
             alt="profile" 
             />
             <div>
             <p style={{marginRight:'10px',color:'#000',fontSize:'16px',}}>{teacherName.current.split('%20')[0]} {teacherName.current.split('%20')[1]}</p>
             {
               !showRatingComponent && <Timmer
               userData={userData} 
               roomID={window.location.hash.substring(1).split('?')[0]}
               socket={socket}
               rootUrl={rootUrl}
               callDuration={duration.current/2}
               ratingTags={ratingTags.current}
               teacherName={teacherName}
               teacherProfilePic={teacherProfilePic}
               setShowRatingComponent={()=>{
                drone.close();
                setCutCall(true);
                setShowRatingComponent(true);
                socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
                }}
               />
             }
             </div>
           </div>
             <button style={{float:'right',marginRight:'10px',backgroundColor:'red',border:'none',color:'white',paddingTop:'5px',paddingRight:'16px',paddingLeft:'16px',paddingBottom:'5px',borderRadius:'16px'}} onClick={()=>{
                countDuration("stop");
                cutAllCall();
             }}>End</button>
             </div> 
           </div>
         )
         }
         {
           !isTeacherJoinTheRoom &&  (
             <div style={{paddingTop:(window.innerHeight/2)-100}}>
               <StudentLoader 
                 userData={userData} 
                 roomID={window.location.hash.substring(1).split('?')[0]}
                 socket={socket}
                 rootUrl={rootUrl}
               />
             </div>
           )
         }
        <video 
        id="localVideo" 
        autoPlay={true} 
        style={{width:0,
         height:0
        }}
        muted>
        </video>
        <video 
         id="remoteVideo" 
         style={{
           width:(isTeacherJoinTheRoom&&isTeacherVedioOn)?'100vw':0,
           height:(isTeacherJoinTheRoom&&isTeacherVedioOn)?'100vh':0,
           objectFit: 'cover',
         }}
         autoPlay={true}>
         </video>
       </div>)
     }
     {
      userData.current[0]==="teacher" && !showRatingComponent&&(<div>
        <div style={{
          height:250,
          width:'100vw'
        }}>
          <img 
          style={{
            height:250,
            width:'100vw'
          }}
          src={`https://solvedudar.com/uploads/doubt_question/${window.location.hash.substring(1).split('?')[0]}.png`} 
          alt="question"
          />
        </div>
        {
          !loading&&(
          <div  style={{
           display: "flex",
           justifyContent:"space-between",
           width:"95%",
           position:"fixed",
           alignItems:'center',
           padding:'10px',
           zIndex:3,
           }}>
             {
               !showRatingComponent && <Timmer
               userData={userData} 
               roomID={window.location.hash.substring(1).split('?')[0]}
               socket={socket}
               rootUrl={rootUrl}
               callDuration={duration.current/2}
               ratingTags={ratingTags.current}
               teacherName={teacherName}
               teacherProfilePic={teacherProfilePic}
               setShowRatingComponent={()=>{
                drone.close();
                setCutCall(true);
                setShowRatingComponent(true);
                socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
                }}
               />
             }
              
              <button
               style={{backgroundColor:'red',border:'none',color:'white',paddingTop:'5px',paddingRight:'16px',paddingLeft:'16px',paddingBottom:'5px',borderRadius:'16px'}}
               onClick={()=>{
                 countDuration("stop");
                 cutAllCall();
              }}>End</button>
          </div>)
        }
        {
          loading&&(
            <div style={{paddingTop:60}}>
            <TeacherLoader/>
            </div>)
        }
        <div>
           <div style={{
             height:loading?0:window.innerHeight-250
           }}>
           <video 
            id="localVideo" 
            autoPlay={true} 
            style={{width:(teacherLocalVedioWidth)?'100vw':0,
             height:(teacherLocalVedioWidth)?window.innerHeight-250:0,
             objectFit: 'cover',
            }}
           muted>
         </video>
         <video 
          id="remoteVideo" 
          style={{
           width:0,
           height:0,
          }}
          autoPlay={true}>
          </video>
          </div>
        </div>
       </div>)
     }
     {
       !showRatingComponent&&
       <div className='videoption'>
        {
          userData.current[0]==="teacher"&& !loading&&(<div>
           {
              isVideoOn&&<img  className="iconStyle" src="./assets/Video off.png" alt="Camera" onClick={()=>{
                toggleVideo();
                setTeacherLocalVedioWidth(!teacherLocalVedioWidth);
              }}/>
           }
           {
               !isVideoOn&&<img  className="iconStyle" src="./assets/Video on.png" alt="Camera" onClick={()=>{
                toggleVideo();
                setTeacherLocalVedioWidth(!teacherLocalVedioWidth);
              }}/>
           }
          {
           !isAudioOn&&<img className="iconStyle" src="./assets/mute microphone.png" alt="Camera" onClick={toggleAudio}/>
          }
         {
          isAudioOn&&<img className="iconStyle" src="./assets/on microphone.png" alt="Camera" onClick={toggleAudio}/>
         }
        </div>)
       }
       <div>
         {
           userData.current[0]==="student" && isTeacherJoinTheRoom && (
           <div>
          {
           !isAudioOn&&<img className="iconStyle" src="./assets/mute microphone.png" alt="Camera" onClick={toggleAudio}/>
          }
         {
          isAudioOn&&<img className="iconStyle" src="./assets/on microphone.png" alt="Camera" onClick={toggleAudio}/>
         }
           </div>)
         }
       </div>
      </div>
      }
      {
        showRatingComponent && (<Rating 
          userData={userData} 
          roomID={window.location.hash.substring(1).split('?')[0]}
          callDuration={duration.current/2}
          ratingTags={ratingTags.current}
          teacherName={teacherName}
          socket={socket}
          rootUrl={rootUrl}
          teacherProfilePic={teacherProfilePic}
          />)
      }
  </div>
  );
} 
const updateDuration = async(rootUrl,roomID)=>{
  if(!localStorage.getItem(`${roomID}`)){
    localStorage.setItem(`${roomID}`,'1');
    try {
      const response = await fetch(
        `${rootUrl}/duration`,
        {
          method: 'POST',
          headers: new Headers({
            'Content-Type': 'application/json',
          }),
          body: JSON.stringify({
            room_id:roomID,
            duration:2
          }),
        },
      );
      let resdata = await response.json();
      if (response.status === 200 || response.ok) {
        if (resdata) {
          console.log('student leave the room',resdata);
        }
      } else {
        console.log('error',resdata);
      }
    } catch (e) {
      console.log('application internal error', e);
    }
  }
}
