import React,{useEffect,useState} from 'react';
import { useAlert } from 'react-alert';

export const Timmer =({userData,roomID,socket,rootUrl,ratingTags,teacherName,teacherProfilePic , setShowRatingComponent})=>{
    const [counter, setCounter] = useState(1);
    const [stopTimmer, setStopTimmer] = useState(false);
    const [validity,setValidity] = useState(null);
    const [timer, setTimer] = useState(null);
    const [numAPICalls,setNumAPICalls] = useState(0);
    const alert = useAlert();
    useEffect(() => {
        if(numAPICalls===1){
          const updateDuration= async(rootUrl,roomID,counter)=>{
            if(!localStorage.getItem(`${roomID}`)){
              localStorage.setItem(`${roomID}`,'1');
              alert.show('you have only 60 seconds left');
              try {
                const response = await fetch(
                  `${rootUrl}/duration`,
                  {
                    method: 'POST',
                    headers: new Headers({
                      'Content-Type': 'application/json',
                    }),
                    body: JSON.stringify({
                      room_id:roomID,
                      duration:counter
                    }),
                  },
                );
                let resdata = await response.json();
                if (response.status === 200 || response.ok) {
                  if (resdata) {
                    console.log(resdata);
                  }
                } else {
                  console.log('error',resdata);
                }
              } catch (e) {
                console.log('application internal error', e);
              }
            }
          }
          let duration=counter+60;
          updateDuration(rootUrl,roomID,duration);
        }
    },[numAPICalls]);
    
    useEffect(() => {
      if (!stopTimmer) {
        let timmer =
          counter > 0 && setInterval(() => setCounter(counter + 1), 1000);
          setTimer(timmer);
        return () => clearInterval(timmer);
      }
    }, [counter, stopTimmer]);
    useEffect(()=>{
      const submitDuration = async data => {
          try {
            const response = await fetch(
              `${rootUrl}/validity`,
              {
                method: 'POST',
                headers: new Headers({
                  'Content-Type': 'application/json',
                }),
                body: JSON.stringify({
                  room_id:roomID,
                }),
              },
            );
            let resdata = await response.json();
            if (response.status === 200 || response.ok) {
              if (resdata) {
                setValidity(resdata);
                console.log(resdata);
              }
            } else {
              console.log('error',resdata);
            }
          } catch (e) {
            console.log('application internal error', e);
          }
        };
        if(userData?.current[0]==="student"){
          submitDuration();
        }
       },[]);
       let today = new Date();
       const currentDate = new Date(`${today.getMonth() + 1}/${today.getDate()}/${today.getFullYear()}`);
       const subDate =validity? new Date(validity.recharge_expiry):0;
       const diff = (subDate - currentDate) / (1000 * 3600 * 24);
       function countTimer(totalSeconds) {
        if(userData?.current[0]==="student" && validity){
          if(validity.subscription_plan==="1"){
            let hour = Math.floor(totalSeconds /3600);
            let minute = Math.floor((totalSeconds - hour*3600)/60);
            let seconds = totalSeconds - (hour*3600 + minute*60);
            if(hour < 10)
              hour = "0"+hour;
            if(minute < 10)
              minute = "0"+minute;
            if(seconds < 10)
              seconds = "0"+seconds;
              return `${hour} : ${minute} : ${seconds}`;
          }else{
            if(validity.subscription_plan==="0" && diff>=0 ){
              if((Number(validity.recharge_minute)+Number(validity.free_minute)-60)===totalSeconds){
                if(numAPICalls===0){
                  setNumAPICalls(1);
                }
              }
              if(Number(validity.recharge_minute)+Number(validity.free_minute)===totalSeconds){
                socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
                socket.emit("bring-student-on-home",{room:roomID,validity:false});
              }
           }else{
            if((Number(validity.free_minute)-60)===totalSeconds){
              if(numAPICalls===0){
                setNumAPICalls(1);
              }
            }
            if(Number(validity.free_minute)===totalSeconds){
                socket.emit('cut_call',{room:window.location.hash.substring(1).split('?')[0]});
                socket.emit("bring-student-on-home",{room:roomID,validity:false});
            }
           }
          }
          }
           var hour = Math.floor(totalSeconds /3600);
           var minute = Math.floor((totalSeconds - hour*3600)/60);
           var seconds = totalSeconds - (hour*3600 + minute*60);
           if(hour < 10)
             hour = "0"+hour;
           if(minute < 10)
             minute = "0"+minute;
           if(seconds < 10)
             seconds = "0"+seconds;
             return `${hour} : ${minute} : ${seconds}`;
        }
        
    return (
    <div style={{marginRight:'10px',marginTop:'-16px'}}>
     <p>{countTimer(counter)}</p>
    </div>
    );
}


