import React,{useEffect,useState} from "react";
import { Rating as StarRating}  from 'react-simple-star-rating';
export const Rating=({userData,roomID,callDuration,ratingTags,teacherName,socket,rootUrl,teacherProfilePic})=>{
    const [rating, setRating] = useState(1);
    const [tagArray,setTagArray] = useState([]);
    const [tagList,setTagList] = useState([]);
    const handleRating = (rate) => {
      if(rating===rate){
        return ;
      }
        setTagList([]);
        setRating(rate);
        const ratingNumber = rate/20;
        if(ratingNumber<=3){
            const filterArr=ratingTags.filter(tag => tag.review_type==="bad");
            console.log('filtered',filterArr);
            setTagArray(filterArr.map(tag =>{
              return {...tag,selected:false,}
            }));
        }else{
          const filterArr=ratingTags.filter(tag => tag.review_type==="good");
          setTagArray(filterArr.map(tag =>{
            return {...tag,selected:false,}
          }));
          console.log('filtered',filterArr);
        }
      }
      
    useEffect(()=>{
          if(userData.current[0]==="student"){
            updateDuration(rootUrl,roomID,callDuration);
          }
    },[]);
    const giveRating=async ()=>{
      try {
        const response = await fetch(
          `${rootUrl}/rating`,
          {
            method: 'POST',
            headers: new Headers({
              'Content-Type': 'application/json',
            }),
            body: JSON.stringify({
              room_id:roomID,
              rating:`${rating/20}`,
              review:tagList.join(),
              user_type:userData.current[0]
            }),
          },
        );
        let resdata = await response.json();
        if (response.status === 200 || response.ok) {
          if (resdata) {
            console.log(resdata);
          }
        } else {
          console.log('error',resdata);
        }
      } catch (e) {
        console.log('application internal error', e);
      }
    }
    if(userData.current[0]==="student"){
      return (
        <div style={{
            backgroundColor:'#e6fff0',
            height:'100vh',
            display:'flex',
            justifyContent: 'center',
        }}>
            <div style={{
                marginTop:'50px'
            }}>
            <p style={{
                textAlign: 'center',
                color:'#000',
               
                fontSize:'20px'
            }}>Rate Your  Recent Tutor</p>
            <div style={{
                display:'flex',
                justifyContent: 'center',
                alignItems: 'center',
                marginTop:'30px'
                }}>   
               <img src={teacherProfilePic.current} alt="teacher" style={{
                   width:100,
                   height:100,
                   borderRadius:'50%'
               }}/>
            </div>
            <p style={{
                textAlign: 'center',
                fontSize:'16px',
                color:'#000',
            }}>
                {teacherName.current.split('%20')[0]} 
               <span> </span>
               {teacherName.current.split('%20')[1]}
            </p>
             <div style={{display: 'flex',justifyContent: 'center',alignItems: 'center'}}>
             <StarRating 
             onClick={handleRating}
             ratingValue={rating}
             tooltipStyle={{
                 backgroundColor:"#fff",
                 color:"#000",
                 fontFamily:"Helvet",
                 fontSize:'16px'
              }}
             />
             </div>
            <div style={{
              width:"100vw",
              display:"flex",
              flexWrap:"wrap",
              alignItems:"center",
              justifyContent:"center",
            }}>
              {
                  tagArray.map(item=>(<label key={item["id "]} style={{
                    border:"1px solid #00c853",
                    padding:'5px ',
                    borderRadius:"10px",
                    margin:"5px",
                    fontSize:"14px",
                    backgroundColor:item.selected?"#00c853":"#fff",
                    color:item.selected?"#fff":"#000",
                  }} 
                  onClick={() => {
                    setTagList([...tagList,item.review]);
                    const updatedArr=tagArray.map(i=>{
                      if(i.review===item.review){
                        return {
                          ...i,
                          selected:true,
                        }
                      }
                      return i;
                    });
                    setTagArray(updatedArr);
                  }}>
                    {item.review}
                 </label>))
              }
            
            </div>
            <div style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              marginTop: '10px',
            }}>
            {
                rating>=20 && ( <label style={{
                  border:"1px solid #fff",
                  padding:'12px 120px',
                  borderRadius:"8px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#00c853",
                  color:"#fff",
                }} 
                onClick={() => {
                  socket.emit("bring-student-on-home",{room:roomID});
                  giveRating();
                }}>
                  Submit
               </label>)
              }
              {
                rating<20 && ( <label style={{
                  border:"1px solid #cccccc",
                  padding:'12px 120px',
                  borderRadius:"10px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#cccccc",
                  color:"#000",
                }} 
                 >
                  Submit
               </label>)
              }
           
            </div>
            <div style={{
              marginTop:10,
              display:"flex",
              flexWrap:"wrap",
              alignItems:"center",
              justifyContent:"center",
            }}>
             <label style={{
                  border:"1px solid #cccccc",
                  padding:'12px 120px',
                  borderRadius:"10px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#00c853",
                  color:"#fff",
                }} 
                onClick={() => {
                  socket.emit("bring-student-on-home",{room:roomID});
                  }}
                 >
                  Skip
              </label>
             </div>
            </div>
        </div>)
      }
      if(userData.current[0]==="teacher"){
      return (
        <div>
        <div style={{
            backgroundColor:'#e6fff0',
            height:'100vh',
            display:'flex',
            justifyContent: 'center',
        }}>
            <div style={{
                marginTop:'50px'
            }}>
            <div style={{
                display:'flex',
                justifyContent: 'center',
                alignItems: 'center',
                marginTop:'80px'
                }}>   
               <img src="https://png.pngtree.com/png-vector/20190214/ourlarge/pngtree-vector-reading-icon-png-image_509597.jpg" alt="teacher" style={{
                   width:150,
                   height:150,
                   borderRadius:'50%'
               }}/>
            </div>
            <p style={{
                textAlign: 'center',
                color:'#000',
            }}>Rate your  recent student</p>
            <div style={{textAlign:'center'}}>
            <StarRating 
             onClick={handleRating}
             ratingValue={rating}
             tooltipStyle={{
                 backgroundColor:"#fff",
                 color:"#000",
                 fontFamily:"Helvet",
                 fontSize:'16px'
              }}
             />
            </div>
             <div style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              marginTop: '10px'
            }}>
              {
                rating>=20 && ( <label style={{
                  border:"1px solid #00c853",
                  padding:'12px 120px',
                  borderRadius:"10px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#00c859",
                  color:"#fff",
                }} 
                onClick={() => {
                    socket.emit("bring-teacher-on-home",{room:roomID});
                    giveRating();
                   }}>
                  Submit
               </label>)
              }
              {
                rating<20 && ( <label style={{
                  border:"1px solid #cccccc",
                  padding:'12px 120px',
                  borderRadius:"10px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#cccccc",
                  color:"#000",
                }} 
                 >
                  Submit
               </label>)
              }
            </div>
            <div style={{
              marginTop:10,
              display:"flex",
              flexWrap:"wrap",
              alignItems:"center",
              justifyContent:"center",
            }}>
             <label style={{
                  border:"1px solid #cccccc",
                  padding:'12px 120px',
                  borderRadius:"10px",
                  margin:"5px",
                  fontSize:"16px",
                  backgroundColor:"#00c853",
                  color:"#fff",
                }} 
                onClick={() => {
                  socket.emit("bring-teacher-on-home",{room:roomID});
                  }}
                 >
                  Skip
              </label>
             </div>
            </div>
            </div>
        </div>);
      }
    
}

const updateDuration= async(rootUrl,roomID,counter)=>{
  if(!localStorage.getItem(`${roomID}`)){
    localStorage.setItem(`${roomID}`,'1');
    try {
      const response = await fetch(
        `${rootUrl}/duration`,
        {
          method: 'POST',
          headers: new Headers({
            'Content-Type': 'application/json',
          }),
          body: JSON.stringify({
            room_id:roomID,
            duration:counter*2
          }),
        },
      );
      let resdata = await response.json();
      if (response.status === 200 || response.ok) {
        if (resdata) {
          console.log(resdata);
        }
      } else {
        console.log('error',resdata);
      }
    } catch (e) {
      console.log('application internal error', e);
    }
  }
}