import React,{useEffect,useState} from 'react';
import { CircularProgressbar ,buildStyles} from 'react-circular-progressbar';
import 'react-circular-progressbar/dist/styles.css';
export const StudentLoader=({userData,roomID,socket,rootUrl})=>{
  const [counter, setCounter] = useState(60);
  const [stopTimmer, setStopTimmer] = useState(false);
  useEffect(() => {
    if (!stopTimmer) {
      let timmer =
        counter > 0 && setInterval(() => setCounter(counter - 1), 1000);
      return () => clearInterval(timmer);
    }
  }, [counter, stopTimmer]);
    useEffect(()=>{
      if(counter===0){
        socket.emit("bring-student-on-home",{room:roomID,teacherConnected:true});
      }
        const submitDuration = async data => {
          try {
            const response = await fetch(
              `${rootUrl}/callCut`,
              {
                method: 'POST',
                headers: new Headers({
                  'Content-Type': 'application/json',
                }),
                body: JSON.stringify({
                  room_id:roomID,
                }),
              },
            );
            let resdata = await response.json();
            if (response.status === 200 || response.ok) {
              if (resdata) {
                console.log('res data',resdata)
              }
            } else {
              console.log('error',resdata);
            }
          } catch (e) {
            console.log('application internal error', e);
          }
        };
        
        if(userData?.current[0]==="student"){
          if(counter === 28){
            submitDuration();
          }
        }
     
    });
  return (
    <div>
    <div style={{display: 'flex', justifyContent: 'center' ,alignItems: 'center'}}>
      <div style={{ width: 150, height: 150 }}>
      <CircularProgressbar 
      maxValue={60} 
      value={counter} 
       text={`${counter} s`}
       strokeWidth={4} 
       styles={buildStyles({
        rotation: 0.25,
        strokeLinecap: 'butt',
        textSize: '16px',
        pathTransitionDuration: 0.5,
        pathColor: `#00662a`,
        textColor: '#00662a',
        trailColor: '#fff',
        backgroundColor: 'red',
      })}
       />
      </div>
    </div>
    <p style={{textAlign:'center',color:'#00662a',fontSize:'20px'}}>Searching teacher for you.</p>
    </div>
  );
}